@import url(https://fonts.googleapis.com/css2?family=DotGothic16&display=swap);
@import url(https://fonts.googleapis.com/css2?family=DotGothic16&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-color: #3c0919;
  color: #e2294f;
  font-size: 1.5rem;
  font-family: DotGothic16;
  letter-spacing: 0.2rem;
  text-align: center;
}
.heading {
  color: #ff2c55;
  text-align: center;
  text-decoration: underline;
}
.content {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
.form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.form button {
  margin-top: 1rem;
}
.details {
  padding: 1.5rem;
}
.names {
  display: flex;
  flex-direction: row;
}
.input {
  padding: 0.8rem 0rem 0.8rem 0rem;
  width: 100%;
  text-align: center;
  font-size: 1.25rem;
  color: #3c0919;
}
.submit {
  margin-top: 1rem;
  width: 120px;
  height: 60px;
  background-color: #ff2c55;
  color: #3c0919;
  font-size: 1rem;
  font-family: DotGothic16;
  border: none;
}
button:hover {
  background-color: #fd4b6e;
}
.content {
  margin-top: 4vh;
}

@media only screen and (max-width: 780px) {
  body {
    font-size: 0.8rem;
  }
  .input {
    padding: 0.5rem 0rem 0.5rem 0rem;
    width: 100%;
    text-align: center;
    font-size: 1rem;
  }
  .names {
    display: flex;
    flex-direction: column;
  }
  .heading {
    color: #ff2c55;
    text-align: center;
    text-decoration: none;
  }
}

.game {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  grid-gap: 0px 0px;
  gap: 0px 0px;
  width: 420px;
  height: 420px;
  grid-template-areas:
    ". . ."
    ". . ."
    ". . .";
}
.buttons {
  width: 120px;
  height: 120px;
  background-color: #ff2c55;
  color: #3c0919;
  border: none;
}

.refresh {
  margin-top: 1rem;
  width: 120px;
  height: 60px;
  background-color: #ff2c55;
  color: #3c0919;
  font-size: 1rem;
  font-weight: bold;
  font-family: DotGothic16;
  border: none;
}
.button {
  padding: 10px;
}

@media only screen and (max-width: 780px) {
  .game {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    grid-gap: 0px 0px;
    gap: 0px 0px;
    width: 300px;
    height: 300px;
    grid-template-areas:
      ". . ."
      ". . ."
      ". . .";
  }
  .buttons {
    width: 80px;
    height: 80px;
    background-color: #ff2c55;
    color: #3c0919;
    border: none;
  }
  .cross {
    width: 50%;
  }
  .circle {
    width: 60%;
  }
}

